.container {
    display: flex;
    justify-content: center;
    gap: 15px;
    background:linear-gradient(270deg, rgba(204,201,255,1) 8%, rgba(0,0,124,1) 51%, rgba(197,242,251,1) 100%);;
    background: rgb(248,249,250);
    background: linear-gradient(270deg, rgba(248,249,250,1) 15%, rgba(0,0,124,1) 40%, rgba(0,0,124,1) 60%, rgba(248,249,250,1) 85%);
    padding: 15px 20px;
    position: sticky;
    top: 0;
    width: 100%;
    z-index: 1000;
    /*box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);*/
}

.container a {
    text-decoration: none;
}

.container button {
    background: #007bff;
    color: #f0f0f0;
    font-weight: bold;
    transition: 0.3s;
    border-radius: 5px;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
}


.container button:hover {
    background: #0056b3;
    color: #ffffff;
    transform: translateY(-2px);
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);
}

.activeLink button {
    background: darkblue !important;
    color: white !important;
    box-shadow: 0px 0px 12px rgba(0, 123, 255, 0.9) !important;
    transform: scale(1.05);
}